.branch__menu{
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  width: 20%;
}
.branch__item{
  width: 100%;
  padding: 5px 0 5px 12px;
  color: black;
  cursor: pointer;
}

.branch__item.active{
  color: white;
  background: black;
  padding: 7px 0 7px 12px;
  border-radius: 3px;
}